.#{$prefix_b}icon {
  @include fa(21px, 1.5rem);
  .#{$prefix_b}fa {
    font-size: inherit;
    line-height: inherit; }
  // Sizes
  &.#{$prefix_b}is-small {
    @include fa(14px, 1rem); }
  &.#{$prefix_b}is-medium {
    @include fa(28px, 2rem); }
  &.#{$prefix_b}is-large {
    @include fa(42px, 3rem); } }
