.#{$prefix_b}media-left,
.#{$prefix_b}media-right {
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0; }

.#{$prefix_b}media-left {
  margin-right: 1rem; }

.#{$prefix_b}media-right {
  margin-left: 1rem; }

.#{$prefix_b}media-content {
  flex-basis: auto;
  flex-grow: 1;
  flex-shrink: 1;
  text-align: left; }

.#{$prefix_b}media {
  align-items: flex-start;
  display: flex;
  text-align: left;
  .#{$prefix_b}content:not(:last-child) {
    margin-bottom: 0.75rem; }
  .#{$prefix_b}media {
    border-top: 1px solid rgba($border, 0.5);
    display: flex;
    padding-top: 0.75rem;
    .#{$prefix_b}content:not(:last-child),
    .#{$prefix_b}control:not(:last-child) {
      margin-bottom: 0.5rem; }
    .#{$prefix_b}media {
      padding-top: 0.5rem;
      & + .#{$prefix_b}media {
        margin-top: 0.5rem; } } }
  & + .#{$prefix_b}media {
    border-top: 1px solid rgba($border, 0.5);
    margin-top: 1rem;
    padding-top: 1rem; }
  // Sizes
  &.#{$prefix_b}is-large {
    & + .#{$prefix_b}media {
      margin-top: 1.5rem;
      padding-top: 1.5rem; } } }
