.#{$prefix_b}level-item {
  align-items: center;
  display: flex;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  .#{$prefix_b}title,
  .#{$prefix_b}subtitle {
    margin-bottom: 0; }
  // Responsiveness
  @include mobile {
    &:not(:last-child) {
      margin-bottom: 0.75rem; } } }

.#{$prefix_b}level-left,
.#{$prefix_b}level-right {
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  .#{$prefix_b}level-item {
    &:not(:last-child) {
      margin-right: 0.75rem; }
    // Modifiers
    &.#{$prefix_b}is-flexible {
      flex-grow: 1; } } }

.#{$prefix_b}level-left {
  align-items: center;
  justify-content: flex-start;
  // Responsiveness
  @include mobile {
    & + .#{$prefix_b}level-right {
      margin-top: 1.5rem; } }
  @include tablet {
    display: flex; } }

.#{$prefix_b}level-right {
  align-items: center;
  justify-content: flex-end;
  // Responsiveness
  @include tablet {
    display: flex; } }

.#{$prefix_b}level {
  @include block;
  align-items: center;
  justify-content: space-between;
  code {
    border-radius: $radius; }
  img {
    display: inline-block;
    vertical-align: top; }
  // Modifiers
  &.#{$prefix_b}is-mobile {
    display: flex;
    & > .#{$prefix_b}level-item {
      &:not(:last-child) {
        margin-bottom: 0; }
      &:not(.#{$prefix_b}is-narrow) {
        flex-grow: 1; } } }
  // Responsiveness
  @include tablet {
    display: flex;
    & > .#{$prefix_b}level-item {
      &:not(.#{$prefix_b}is-narrow) {
        flex-grow: 1; } } } }
