// Components

.#{$prefix_b}hero-video {
  @include overlay;
  overflow: hidden;
  video {
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    position: absolute;
    top: 50%;
    transform: translate3d(-50%, -50%, 0); }
  // Modifiers
  &.#{$prefix_b}is-transparent {
    opacity: 0.3; }
  // Responsiveness
  @include mobile {
    display: none; } }

.#{$prefix_b}hero-buttons {
  margin-top: 1.5rem;
  // Responsiveness
  @include mobile {
    .#{$prefix_b}button {
      display: flex;
      &:not(:last-child) {
        margin-bottom: 0.75rem; } } }
  @include tablet {
    display: flex;
    justify-content: center;
    .#{$prefix_b}button:not(:last-child) {
      margin-right: 1.5rem; } } }

// Containers

.#{$prefix_b}hero-head,
.#{$prefix_b}hero-foot {
  flex-grow: 0;
  flex-shrink: 0; }

.#{$prefix_b}hero-body {
  flex-grow: 1;
  flex-shrink: 0;
  padding: 3rem 1.5rem;
  // Responsiveness
  @include from($widescreen) {
    padding-left: 0;
    padding-right: 0; } }

// Main container

.#{$prefix_b}hero {
  align-items: stretch;
  background-color: $white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .#{$prefix_b}nav {
    background: none;
    box-shadow: 0 1px 0 rgba($border, 0.3); }
  .#{$prefix_b}tabs {
    ul {
      border-bottom: none; } }
  // Colors
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    $color-invert: nth($pair, 2);
    &.#{$prefix_b}is-#{$name} {
      background-color: $color;
      color: $color-invert;
      a,
      strong {
        color: inherit; }
      .#{$prefix_b}title {
        color: $color-invert; }
      .#{$prefix_b}subtitle {
        color: rgba($color-invert, 0.9);
        a,
        strong {
          color: $color-invert; } }
      .#{$prefix_b}nav {
        box-shadow: 0 1px 0 rgba($color-invert, 0.2); }
      .#{$prefix_b}nav-menu {
        @include mobile {
          background-color: $color; } }
      a.#{$prefix_b}nav-item,
      .#{$prefix_b}nav-item a:not(.#{$prefix_b}button) {
        color: rgba($color-invert, 0.7);
        &:hover,
        &.#{$prefix_b}is-active {
          color: $color-invert; } }
      .#{$prefix_b}tabs {
        a {
          color: $color-invert;
          opacity: 0.9;
          &:hover {
            opacity: 1; } }
        li {
          &.#{$prefix_b}is-active a {
            opacity: 1; } }
        &.#{$prefix_b}is-boxed,
        &.#{$prefix_b}is-toggle {
          a {
            color: $color-invert;
            &:hover {
              background-color: rgba($black, 0.1); } }
          li.#{$prefix_b}is-active a {
            &,
            &:hover {
              background-color: $color-invert;
              border-color: $color-invert;
              color: $color; } } } }
      // Modifiers
      &.#{$prefix_b}is-bold {
        $gradient-top-left: darken(saturate(adjust-hue($color, -10deg), 10%), 10%);
        $gradient-bottom-right: lighten(saturate(adjust-hue($color, 10deg), 5%), 5%);
        background-image: linear-gradient(141deg, $gradient-top-left 0%, $color 71%, $gradient-bottom-right 100%); }
      // Responsiveness
      @include mobile {
        .#{$prefix_b}nav-toggle {
          span {
            background-color: $color-invert; }
          &:hover {
            background-color: rgba($black, 0.1); }
          &.#{$prefix_b}is-active {
            span {
              background-color: $color-invert; } } }
        .#{$prefix_b}nav-menu {
          .#{$prefix_b}nav-item {
            border-top-color: rgba($color-invert, 0.2); } } } } }
  // Sizes
  &.#{$prefix_b}is-medium {
    @include tablet {
      .#{$prefix_b}hero-body {
        padding-bottom: 9rem;
        padding-top: 9rem; } } }
  &.#{$prefix_b}is-large {
    @include tablet {
      .#{$prefix_b}hero-body {
        padding-bottom: 18rem;
        padding-top: 18rem; } } }
  &.#{$prefix_b}is-fullheight {
    min-height: 100vh;
    .#{$prefix_b}hero-body {
      align-items: center;
      display: flex;
      & > .#{$prefix_b}container {
        flex-grow: 1;
        flex-shrink: 1; } } } }
