// Display

$displays: 'block' 'flex' 'inline' 'inline-block' 'inline-flex';

@each $display in $displays {
  .#{$prefix_b}is-#{$display} {
    display: #{$display}; }
  .#{$prefix_b}is-#{$display}-mobile {
    @include mobile {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-tablet {
    @include tablet {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-tablet-only {
    @include tablet-only {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-touch {
    @include touch {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-desktop {
    @include desktop {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-desktop-only {
    @include desktop-only {
      display: #{$display} !important; } }
  .#{$prefix_b}is-#{$display}-widescreen {
    @include widescreen {
      display: #{$display} !important; } } }

// Float

.#{$prefix_b}is-clearfix {
  @include clearfix; }

.#{$prefix_b}is-pulled-left {
  float: left; }

.#{$prefix_b}is-pulled-right {
  float: right; }

// Overflow

.#{$prefix_b}is-clipped {
  overflow: hidden !important; }

// Overlay

.#{$prefix_b}is-overlay {
  @include overlay; }

// Text

.#{$prefix_b}has-text-centered {
  text-align: center; }

.#{$prefix_b}has-text-left {
  text-align: left; }

.#{$prefix_b}has-text-right {
  text-align: right; }

// Visibility

.#{$prefix_b}is-hidden {
  display: none !important; }

.#{$prefix_b}is-hidden-mobile {
  @include mobile {
    display: none !important; } }

.#{$prefix_b}is-hidden-tablet {
  @include tablet {
    display: none !important; } }

.#{$prefix_b}is-hidden-tablet-only {
  @include tablet-only {
    display: none !important; } }

.#{$prefix_b}is-hidden-touch {
  @include touch {
    display: none !important; } }

.#{$prefix_b}is-hidden-desktop {
  @include desktop {
    display: none !important; } }

.#{$prefix_b}is-hidden-desktop-only {
  @include desktop-only {
    display: none !important; } }

.#{$prefix_b}is-hidden-widescreen {
  @include widescreen {
    display: none !important; } }

// Other

.#{$prefix_b}is-disabled {
  pointer-events: none; }

.#{$prefix_b}is-marginless {
  margin: 0 !important; }

.#{$prefix_b}is-paddingless {
  padding: 0 !important; }

.#{$prefix_b}is-unselectable {
  @include unselectable; }
